import { usePage } from "app/hooks/usePage"
import { SButton, SMenu } from "./StartMenu.style"

interface Props {
    start: () => void
}
export const StartMenu = ({ start }: Props) => {
    const { changePage } = usePage()
    return (
        <SMenu>
            <SButton onClick={start}> </SButton>
            <SButton onClick={() => changePage("storage")}></SButton>
            <SButton onClick={() => changePage("faq")}></SButton>
        </SMenu>
    )
}
